<template>
  <div class="flex xl:flex-row flex-col justify-between mb-8">
    <Breadcrumb pageTitle="Shipping Label Request" :backTo="{ name: 'report' }" :items="breadcrumb" />
  </div>
  <Card>
    <div class="flex flex-col mt-4">
      <div class="flex flex-row 2xl:flex-nowrap xl:flex-nowrap lg:flex-nowrap flex-wrap">
        <div class="flex-grow md:mr-3 md:mb-0 mb-3">
          <Input placeholder="Search everything" class="w-full" icon v-model="data.search">
            <template #icon>
              <span class="material-icons">search</span>
            </template>
          </Input>
        </div>
        <div class="flew-grow-0">
          <GroupContainer>
            <Button color="white" view="outlined" class="flex-auto" @click="filterDateFocus" @blur="filterDateFocus">
              <DatePicker v-model="data.date" v-bind="filterDateOptions" type="daterange" ref="filterDateRef" class="hide-picker" /> Select Date
            </Button>
            <Dropdown trigger="click" size="medium" :hide-on-click="false" :disabled="loadingPaymentMethods">
              <Button color="white" view="outlined" class="flex-auto dropdown dropdown-arrow" :loading="loadingPaymentMethods">Payout Method</Button>
              <template #dropdown>
                <DropdownMenu>
                  <DropdownItem v-for="(paymentMethod, index) in statePaymentMethods" :key="index">
                    <Checkbox color="teal" v-model="data.payment_method" :value="paymentMethod.name">
                      {{ paymentMethod.name }}
                    </Checkbox>
                  </DropdownItem>
                </DropdownMenu>
              </template>
            </Dropdown>
            <Dropdown trigger="click" size="medium" :hide-on-click="false">
              <Button color="white" view="outlined" class="flex-auto dropdown dropdown-arrow">Courier</Button>
              <template #dropdown>
                <DropdownMenu>
                  <DropdownItem v-for="(courier, courierIndex) in couriers" :key="courierIndex">
                    <Checkbox color="teal" v-model="data.shipping_courier" :value="courier.name">
                      {{ courier.name }}
                    </Checkbox>
                  </DropdownItem>
                </DropdownMenu>
              </template>
            </Dropdown>
          </GroupContainer>
        </div>
      </div>
      <div class="flex items-center gap-x-1 mt-5">
         <div class="inline-block" v-for="(filter, filterKey) of filterList" :key="filterKey">
            <Button v-if="filter.value" color="grey" view="smooth" dense class="mx-1">
              <b>{{filter.name}}:</b> {{ filter.value }}<i class="material-icons text-sm ml-2" @click="clearFilter(filter)">close</i>
            </Button>
          </div>
         <div class="inline-block ml-3" v-if="isFiltered">
            <Button @click="clearAllFilter" color="teal" view="smooth" dense>
              <i class="material-icons text-sm mr-1">close</i>Clear all
            </Button>
          </div>
      </div>
    </div>
    <div class="flex flex-row mt-4" v-if="selectedItems.length > 0">
      <Button class="mr-3" view="smooth" color="teal" @click="batchUpdatePrintStatus" :loading="loading.update" dense>Change Status Print Batch</Button>
      <Button class="mr-3" view="smooth" color="teal" :loading="loading.print.shipping_kit" dense @click="batchPrintShippingKit"><i class="material-icons text-lg mx-1">print</i> Print Batch Shipping Kit</Button>
      <Button class="mr-3" view="smooth" color="teal" :loading="loading.print.address_label" dense @click="batchPrintAddressLabel"><i class="material-icons text-lg mx-1">print</i> Print Batch Address Label</Button>
    </div>
    <div class="flex flex-col mt-6">
      <Table ref="refBuybackTable" class="mb-6" v-table="tableConfig" v-model:data="state.data" v-loading="loading.show" :row-key="getRowKey" @selection-change="tableOnSelect">
        <TableColumn type="expand">
          <template #default="scope">
            <div class="grid grid-cols-8 px-20 gap-6">
              <div class="col-start-1">
                <span class="font-semibold block mb-3">Type</span>
                <span v-if="scope.row.affiliate_id != null">Affiliate Buyback</span>
                <span v-else-if="scope.row.type === '4'">Custom Quote</span>
                <span v-else-if="scope.row.type === '3'">Easy Quote</span>
                <span v-else>Mail-In Buyback</span>
              </div>
              <div class="col-span-2">
                <p class="font-semibold mb-2">Devices</p>
                <p class="my-2" v-for="(item, itemIndex) of scope.row.details" :key="itemIndex">{{ item.product_name }}, {{item.qty}}</p>
              </div>
            </div>
          </template>
        </TableColumn>
        <TableColumn type="selection" :reserve-selection="true" :selectable="selectableRow" />
        <TableColumn prop="box_id" label="Buyback ID" width="250">
          <template #default="scope">
            <router-link tag="a" class="font-bold" :to="{ name: 'buybackOnlineDetail', params: { buybackId: scope.row.box_id } }">{{ scope.row.box_id }}</router-link>
            <small v-if="!!scope.row.is_print" class="block mt-1 text-green-04">Printed on {{scope.row.printed_at || ''}}</small>
            <small v-else class="block mt-1 text-red-03">Not Printed</small>
          </template>
        </TableColumn>
        <TableColumn label="Name">
          <template #default="scope">
            <span class="block">{{scope.row.ship_to}}</span>
            <span class="block">{{scope.row.customer.email}}</span>
          </template>
        </TableColumn>
        <TableColumn prop="payment_method" label="Payout Method" width="150" />
        <TableColumn prop="order_date" label="Status Date" />
        <TableColumn label="Ship To">
          <template #default="scope">
            <span class="block">{{scope.row.ship_to}}</span>
            <span class="block">{{phoneNumber(scope.row.phone)}}</span>
          </template>
        </TableColumn>
        <TableColumn prop="shipping_courier" label="Courier" />
        <TableColumn prop="qty" label="Qty" width="50" align="right" />
        <TableColumn prop="sub_total" label="Total" align="right">
          <template #default="scope">
            <Currency :value="scope.row.sub_total" />
          </template>
        </TableColumn>
      </Table>
      <Pagination v-model:page="data.page" v-model:limit="data.limit" :data="state" />
    </div>
  </Card>
</template>

<script>
import { defineComponent, ref, watch } from 'vue'
import { useUserShippingLabel } from 'composable/report'
import { usePaymentMethods }  from 'composable/buyback'
import Card from 'bbUi/components/card/card.vue'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import Input from 'bbUi/components/input/input.vue'
import Alert from 'bbUi/components/alert/alert.vue'
import Button from 'bbUi/components/button/button.vue'
import Pagination from 'components/Pagination.vue'
import Badge from 'bbUi/components/badge/badge.vue'
import Breadcrumb from 'components/Breadcrumb.vue'
import GroupContainer from 'bbUi/components/group-container/group-container.vue'
import { format } from 'utils/date'
import debounce from 'utils/debounce'
import { phoneNumber } from 'utils/number'
import { ElMessage } from 'element-plus'
import Currency from 'components/Currency.vue'

export default defineComponent({
  components: {
    Card,
    Checkbox,
    Input,
    Alert,
    Button,
    Badge,
    Breadcrumb,
    Pagination,
    GroupContainer,
    Currency
  },
  async setup(props, { emit }) {
    const { data, actionAll, actionBatchStatusPrintUpdate, actionBatchPrintShippingKit, actionBatchPrintAddressLabel, loading, state,  filterList, clearFilter, clearAllFilter, isFiltered, filterDateRef, filterDateOptions, filterDateFocus } = useUserShippingLabel()
    const { state: statePaymentMethods, action: actionPaymentMethods, loading: loadingPaymentMethods } = usePaymentMethods('online')
    const refBuybackTable = ref(null)
    const selectedItems = ref([])
    const couriers = [{
      id: 'usps',
      name: 'USPS'
    },{
      id: 'fedex',
      name: 'FedEx'
    },{
      id: 'ups',
      name: 'UPS'
    }]
    const breadcrumb = [{
      text: 'Reports',
      to: {
        name: 'report'
      }
    }, {
      text: 'Shipping Label Request'
    }]
    await Promise.all([
      actionAll(),
      actionPaymentMethods()
    ])
    watch(() => ({
      page: data.page,
      limit: data.limit,
      search: data.search,
      payment_method: data.payment_method,
      date: data.date,
      shipping_courier: data.shipping_courier
    }), debounce(async() => {
      await actionAll()
    }, 500), {
      deep: true
    })
    const getRowKey = (row) => row.box_id
    const selectableRow = (row) => {
      const selectedData = selectedItems.value
      // if( selectedData.length > 0 ) return selectedData[0].status_id === row.status_id
      return true
    }
    const tableOnSelect = (buybacks) => {
      selectedItems.value = buybacks
    }
    // const resetSelectedItems = () => {
    //   refBuybackTable.value.clearSelection()
    //   selectedItems.value = []
    // }
    const batchUpdatePrintStatus = async () => {
      const buybacks = selectedItems.value
      await actionBatchStatusPrintUpdate(buybacks, async () => {
        ElMessage({
          message: 'Buybacks updated!',
          type: 'success'
        })
        await actionAll()
      })
    }
    const batchPrintShippingKit = async() => {
      const buybacks = selectedItems.value
      await actionBatchPrintShippingKit(buybacks)
    }
    const batchPrintAddressLabel = async() => {
      const buybacks = selectedItems.value
      await actionBatchPrintAddressLabel(buybacks)
    }

    return {
      refBuybackTable,
      breadcrumb,
      couriers,
      state,
      statePaymentMethods,
      loading,
      loadingPaymentMethods,
      data,
      selectedItems,
      filterList,
      clearFilter,
      clearAllFilter,
      isFiltered,
      filterDateRef,
      filterDateOptions,
      filterDateFocus,
      format,
      tableConfig: {
        isShowSelectionAll: true
      },
      getRowKey,
      selectableRow,
      tableOnSelect,
      batchUpdatePrintStatus,
      batchPrintShippingKit,
      batchPrintAddressLabel,
      phoneNumber
    }
  }
})
</script>